@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  font-family: Roboto;
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.color-theme {
  background-color: white;
}

.container {
  padding-right: 5px;
  padding-left: 5px;
  margin-right: 15px;
  margin-left: 15px;
}

.flex-container {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
}

@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}
@media (max-width: 768px) {
  .flex-container {
    display: flex;
    flex-direction: row;
  }
}
@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}


.main-app {
  display: flex;
  flex-flow: row nowrap;
  width: 100%;
}

.left-main-app {
  width: auto;
}

.main-component {
  margin-left: 15px;
}

span.work-theme {
  background-color: blue;
  color: white;
}

div.work-task {
  border-top-width: 3px;
  border-top-color: blue;
}

span.personal-theme {
  background-color: orange;
  color: white;
}

div.personal-task {
  border-top-width: 3px;
  border-top-color: orange;
}

div.active-navigation {
  background-color: darkgray;
}

.list-navigation {
  position: -webkit-sticky;
  position: sticky;
  left: 0;
  top: 70px;
  height: 90vh;
  display: flex;
  flex-flow: column nowrap;
  width: 260px;
  height: 100vh;
  /* background-color: #c0c0c0; */
  background-color: white;
  border-right: 1px solid rgba(0, 0, 0, 0.12);
  transition: all 1s;
  overflow: hidden;
  z-index: 9;
}

.top-title {
  display: flex;
  height: 80px;
  align-items: center;
  font-size: 1.3em;
  font-weight: 500;
  padding: 0 20px;
}

.create-task-button {
  display: flex;
  width: 100%;
  align-items: center;
  padding: 10px 15px 10px 20px;
  cursor: pointer;
  font-size: 0.9em;
  transition: all 0.25s;
}

.create-task-button:hover {
  background-color: whitesmoke;
}

.add-icon {
  margin-left: auto;
}

.close-modal-btn {
  position: absolute;
  right: 20px;
  top: 15px;
  font-size: 22px;
  color: rgba(0, 0, 0, 0.8);
}

.navigation-items {
  display: flex;
  flex-flow: column nowrap;
  width: 100%;
  padding: 0;
  margin-top: 20px;
  height: calc(100vh - 80px);
  max-height: calc(100vh - 80px);
  overflow-y: auto;
  /* background-color: #dcdcdc; */
}

.navigation-listitems {
  display: flex;
  width: 100%;
  flex-flow: row nowrap;
  padding: 10px 20px;
  align-items: center;
  transition: all 0.25s;
  text-transform: capitalize;
  text-decoration: none;
}

.navigation-listitems:hover {
  background-color: whitesmoke;
  cursor: pointer;
}

.active-nav {
  background-color: whitesmoke;
  text-decoration: none;
}

.navigation-title {
  font-size: 0.9em;
  font-weight: 100;
  float: left;
}

.total-task {
  margin-left: auto;
  font-size: 0.9em;
}

.modal-title {
  display: flex;
  align-items: center;
  padding: 25px;
  border: solid;
  border-width: 1px;
  border-color: transparent transparent rgba(0, 0, 0, 0.12) transparent;
}

.modal-title h2 {
  font-weight: 100;
}

.task-form {
  display: flex;
  width: 100%;
  padding: 20px;
  min-height: 300px;
  max-height: 500px;
  flex-flow: column nowrap;
  overflow: hidden;
  overflow-y: auto;
  justify-content: space-evenly;
}

.task-form input {
  width: 100%;
  font-size: 14px;
  padding: 18px 10px;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 5px;
  outline: none;
  margin-bottom: 20px;
  transition: all 0.25s;
  resize: none;
}

textarea {
  border-color: inherit;
}

.task-form .select-time {
  width: 100%;
}
.task-form .select {
  width: 100%;
  font-size: 14px;
  border: solid 1px transparent;
  margin-bottom: 20px;
  border-radius: 5px;
  outline: none;
  transition: all 0.25s;
  box-shadow: 0;
}

.task-form input:focus,
.task-form textarea:focus,
task-form .select:focus {
  border-color: blueviolet;
}

.add-task-btn {
  background-color: blueviolet;
  padding: 10px 16px;
  border: none;
  border-radius: 15px;
  color: white;
  float: right;
  margin-left: auto;
  font-size: 18px;
  transition: all 0.25s;
}

.go-back-btn {
  cursor: pointer;
  color: rgba(0, 0, 0, 0.4);
  margin-top: -5px;
  background-color: rgba(0, 0, 0, 0.12);
  border-radius: 50%;
  padding: 6px 3px 3px 12px;
  margin-right: 15px;
}

.react-calendar {
  width: 350px;
  max-width: 100%;
  background: white;
  border: none;
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.125em;
  border-radius: 15px;
  padding: 20px;
  margin-top: 15px;
}

.modal-calender {
  width: 100%;
  max-width: 100%;
  background: white;
  border: none;
  font-family: Arial, Helvetica, sans-serif;
  line-height: 2.75em;
  padding: 20px 20px;
  margin-top: -10px;
}

.react-calendar--doubleView {
  width: 700px;
}
.react-calendar--doubleView .react-calendar__viewContainer {
  display: flex;
  margin: -0.5em;
}
.react-calendar--doubleView .react-calendar__viewContainer > * {
  width: 50%;
  margin: 0.5em;
}

.react-calendar,
.react-calendar *,
.react-calendar *:before,
.react-calendar *:after {
  box-sizing: border-box;
}

.react-calendar button {
  margin: 0;
  border: 0;
  outline: none;
  border-radius: 35px;
}

.react-calendar button:enabled:hover {
  cursor: pointer;
  background-color: blueviolet;
  color: white;
}

.header-top {
  width: 100%;
  padding: 10px;
  background-color: white;
  border-bottom: 1px solid rgba(0, 0, 0, 0.12);
  display: flex;
  align-items: center;
  flex-flow: row nowrap;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 99;
}

.left-section {
  display: flex;
  flex-flow: row nowrap;
  margin-left: 15px;
  align-items: center;
  justify-content: flex-start;
}

.Logo-text {
  font-size: 1.4em;
  font-weight: 600;
  font-style: oblique;
}

.color-red {
  color: blue;
}
.middle-section {
  display: flex;
  flex: 1 1;
  justify-content: center;
}

.search-input {
  width: 80%;
  background-color: none;
  border: none !important;
  outline: none;
  padding: 10px;
}

.search-input:hover {
  border: none;
}

.right-section {
  display: flex;
  margin-left: 20px;
  margin-right: 15px;
  float: right;
  align-items: center;
  max-width: 160px;
}

.avatar-color {
  background-color: blueviolet;
  color: white;
}

p.username {
  margin-left: 5px;
  font-size: 0.8em;
  text-transform: capitalize;
}

.tasks {
  display: flex;
  width: 100%;
  flex-flow: row wrap;
  align-items: center;
  justify-content: flex-start;
  padding: 15px;
}

.task-item-div {
  flex: 25% 1;
  max-width: 330px;
  margin: 15px;
  display: block;
  background-color: white;
  border: solid;
  border-width: 1px;
  border-color: rgba(0, 0, 0, 0.12);
  border-radius: 5px;
}

.task-item-div:hover {
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.12);
}

.task-item-content {
  padding: 15px;
}

.task-item-content ul {
  list-style: none;
  display: block;
}

.task-item-content ul li {
  padding: 10px 5px;
}

.task-item-content ul li:last-child {
  padding: 5px 5px;
  float: right;
}

span.task-type {
  padding: 10px 15px;
  font-size: 0.9em;
  text-transform: capitalize;
  border-radius: 7px;
}

.task-title {
  font-size: 1em;
  font-weight: 400;
  line-height: 1.5em;
}

.completed-task-true {
  text-decoration: line-through;
}

.task-date {
  font-size: 0.9em;
  color: rgba(0, 0, 0, 0.8);
  line-height: 1.6em;
}

.undone-task {
  color: lightgrey;
  cursor: pointer;
  transition: all 0.25s;
}

.undone-task:hover {
  color: red;
}

.completed-task {
  color: red;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: none;
}

.no-task {
  width: 100%;
  height: 300px;
  background-color: whitesmoke;
  border: 1px solid rgba(0, 0, 0, 0.12);
  display: flex;
  font-size: 1.2em;
  align-items: center;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 50px;
  line-height: 1.6em;
}

